'use client';
import { useState } from "react";
import { QrCode, CheckCircle, XCircle } from "lucide-react";

export default function QrCodeWhatsApp() {
  // Exemplo de estados: altere para buscar real!
  const [qrCode, setQrCode] = useState<string | null>(null); // base64 do QR Code
  const [isConnected, setIsConnected] = useState(false);

  // Simula geração do QR Code (substitua pelo fetch real)
  const handleGenerateQrCode = async () => {
    setQrCode("data:image/png;base64,SEU_BASE64_AQUI"); // troque pelo QR code real!
    setIsConnected(false);
  };

  return (
    <div className="flex flex-col items-center w-full">
      {/* CARD STATUS */}
      <div className="bg-white rounded-2xl shadow-lg px-8 py-10 w-full max-w-2xl mb-8 border">
        <div className="flex justify-between items-center mb-7">
          <h2 className="text-3xl font-extrabold flex items-center gap-3">
            <QrCode className="text-blue-600" size={36} />
            Status da Conexão WhatsApp
          </h2>
          {isConnected ? (
            <span className="bg-green-100 text-green-700 px-4 py-2 rounded-lg flex items-center gap-2 font-semibold text-sm">
              <CheckCircle className="w-5 h-5" /> Conectado
            </span>
          ) : (
            <span className="bg-red-100 text-red-700 px-4 py-2 rounded-lg flex items-center gap-2 font-semibold text-sm">
              <XCircle className="w-5 h-5" /> Desconectado
            </span>
          )}
        </div>
        <div className="flex flex-col items-center justify-center mb-7">
          {/* QR CODE */}
          <div className="bg-gray-100 rounded-xl flex items-center justify-center mb-4"
               style={{ width: 220, height: 220 }}>
            {qrCode ? (
              <img src={qrCode} alt="QR Code" className="w-52 h-52 object-contain" />
            ) : (
              <QrCode className="text-gray-300" size={100} />
            )}
          </div>
          <div className="font-bold text-xl mb-2">
            {isConnected ? "WhatsApp Conectado" : "Conectar WhatsApp"}
          </div>
          <p className="text-gray-600 text-base mb-4">
            {isConnected
              ? "Seu WhatsApp está conectado!"
              : "Escaneie o QR code com seu WhatsApp para conectar sua conta."}
          </p>
          {!isConnected && (
            <button
              onClick={handleGenerateQrCode}
              className="bg-blue-700 hover:bg-blue-800 transition text-white px-10 py-3 rounded-xl text-lg font-bold shadow"
            >
              Gerar QR Code
            </button>
          )}
        </div>
      </div>

      {/* COMO CONECTAR */}
      <div className="bg-white rounded-2xl shadow-lg px-8 py-7 w-full max-w-2xl border">
        <h3 className="text-2xl font-extrabold mb-5">Como Conectar</h3>
        <ol className="list-decimal ml-5 space-y-3 text-lg text-gray-700">
          <li>Abra o WhatsApp no seu celular</li>
          <li>Toque em <b>Menu</b> (3 pontinhos) {'>'} <b>Dispositivos conectados</b></li>
          <li>Toque em <b>Conectar um dispositivo</b></li>
          <li>Escaneie o QR code exibido acima</li>
        </ol>
      </div>
    </div>
  );
}
